/*! Flickity v3.0.0
https://flickity.metafizzy.co
---------------------------------------------- */

.flickity-enabled {
    position: relative;
  }
  
  .flickity-enabled:focus { outline: none; }
  
  .flickity-viewport {
    overflow: hidden;
    position: relative;
    height: 100%;
    touch-action: pan-y;
  }
  
  .flickity-slider {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
  }
  
  .flickity-rtl .flickity-slider {
    left: unset;
    right: 0;
  }
  
  /* draggable */
  
  .flickity-enabled.is-draggable {
    -webkit-tap-highlight-color: transparent;
    user-select: none;
  }
  
  .flickity-enabled.is-draggable .flickity-viewport {
    cursor: move;
    cursor: grab;
  }
  
  .flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
    cursor: grabbing;
  }
  
  /* ---- flickity-cell ---- */
  
  .flickity-cell {
    position: absolute;
    left: 0;
  }
  
  .flickity-rtl .flickity-cell {
    left: unset;
    right: 0;
  }
  
  /* ---- flickity-button ---- */
  
  .flickity-button {
    position: absolute;
    background: hsl(0 0% 100% / 75%);
    border: none;
    color: #333;
  }
  
  .flickity-button:hover {
    background: white;
    cursor: pointer;
  }
  
  .flickity-button:focus {
    outline: none;
    box-shadow: 0 0 0 5px #19F;
  }
  
  .flickity-button:active {
    opacity: 0.6;
  }
  
  .flickity-button:disabled {
    opacity: 0.3;
    cursor: auto;
    /* prevent disabled button from capturing pointer up event. #716 */
    pointer-events: none;
  }
  
  .flickity-button-icon {
    fill: currentColor;
  }
  
  /* ---- previous/next buttons ---- */
  
  .flickity-prev-next-button {
    top: 50%;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    /* vertically center */
    transform: translateY(-50%);
  }
  
  .flickity-prev-next-button.previous { left: 10px; }
  .flickity-prev-next-button.next { right: 10px; }
  /* right to left */
  .flickity-rtl .flickity-prev-next-button.previous {
    left: auto;
    right: 10px;
  }
  
  .flickity-rtl .flickity-prev-next-button.next {
    right: auto;
    left: 10px;
  }
  
  .flickity-prev-next-button .flickity-button-icon {
    position: absolute;
    left: 20%;
    top: 20%;
    width: 60%;
    height: 60%;
  }
  
  /* ---- page dots ---- */
  
  .flickity-page-dots {
    position: absolute;
    width: 100%;
    bottom: -25px;
    text-align: center;
    display: flex;
    //justify-content: center;
    //flex-wrap: wrap;
  }
  
  .flickity-rtl .flickity-page-dots { direction: rtl; }
  
  .flickity-page-dot {
    display: block;
    width: 150px;
    height: 10px;
    padding: 0;
    margin: 0;
    background: #EBECEB;
    background: linear-gradient(0deg, rgba(235,236,235,0) 0%, rgba(235,236,235,0) 39.9%, rgba(235,236,235,1) 40%, rgba(235,236,235,1) 60%, rgba(235,236,235,0) 60.1%, rgba(235,236,235,0) 100%);
    //border-radius: 50%;
    cursor: pointer;
    appearance: none;
    border: none;
    text-indent: -9999px;
    overflow: hidden;
  }
  
  .flickity-rtl .flickity-page-dot {
    text-indent: 9999px;
  }
  
  .flickity-page-dot:focus {
    outline: none;
    //box-shadow: 0 0 0 5px #19F;
  }
  
  .flickity-page-dot.is-selected {
    background: $primary;
    background: linear-gradient(0deg, rgba($primary, 0) 0%, rgba($primary, 0) 39.9%, rgba($primary,1) 40%, rgba($primary,1) 60%, rgba($primary, 0) 60.1%, rgba($primary, 0) 100%);
  }
  
  .flickity-slider article {
    min-height: 100%;
  }